import React from 'react';
import me from '../img/me.png'

export default () => {
  return (
    <div className="home">
      <div className="home-content">
        <p>Hello, 我是肖俊，欢迎来到我的主页。</p>
        <p>两年前，我从中山大学博士毕业，然后去到美国做博士后研究员。在那里，我对互联网产生了极大的兴趣，从美国归来后，我就开始自学相关互联网技术。</p>
      
        <p>学习的资料来源于Udemy， Udacity， Lynda， 慕课网..., 自学一年期间，结合我的科研经验，我做了edsolver.com以及 lalsci.com, 这两个网站主要是为了解决我在读博士期间遇到的问题，一个是晶体衍射的标定问题，另一个是我们领域文献查找的准确性和文献分类的问题。</p>

        <p>我学到的大部分知识都来自于在线教育平台，我用过多数知名的在线教育平台，而且付费购买了相关课程，或者成为它们的会员。用现在时髦的话说，我是在线教育的重度用户，因此，我比较了解他们之间的差异以及优缺点。我选取了14个知名在线教育平台，把自己对在线教育平台的认识分成8个部分来描述。</p>
        
        <p>热爱教育，热爱互联网。</p>
      </div>
      
      <div>
        <img className="App-me" src={me} alt="me"/>
      </div>
          
    </div>
  )
}


